import React,{Component} from 'react';
import BaseInfo from './components/BaseInfo.jsx';
import ScoreSummary from './components/ScoreSummary';
import FoldableText from './components/FoldableText';
import Artist from './components/Artist';
import ScoreDistribute from './components/ScoreDistribute';
import TagList from './components/TagList';
import CommentList from './components/CommentList';
import LineLink from '../../components/LineLink';
import {Link} from 'react-router-dom';
import ImgSlide from './container/ImgSlide';
import './Detail.css';
import '../../common.css';
class Detail extends Component{
  state={
    showImage:false
  }
  taggleImage=()=>{
    let showImage  = this.state.showImage;
    this.setState({showImage:!showImage});
  }
  render(){
    let showImage = this.state.showImage;
    return (
      <div className="detail">
        <div className="detail__top">
          <div className="tOperator">
            <div className="tOperator__icon tOperator__icon--back" onClick={()=>window.history.back()}/>
            <div className="tOperator__icon tOperator__icon--share"/>
          </div>
          <BaseInfo showImage = {this.taggleImage}/>
        </div>
        <div className="detail__content">
          <div className="detail__module">
            <ScoreSummary/>
          </div>
          <div className="detail__module">
            <FoldableText>
              <div className="detail__overview">
                 onSelect 
                 Select  bug  GIF  onSelect  state.selection 
                 fireOnSelect  setState 
                 React  setState  scheduleState 
              </div>
            </FoldableText>
          </div>
          <div className="detail__module">
            <h3 className="detail__module--title"></h3>
            <Artist/>
          </div>
          <div className="detail__module">
            <h3 className="detail__module--title"></h3>
            <ScoreDistribute/>
          </div>
          <div className="detail__module">
            <TagList/>
          </div>
          <div className="detail__module">
            <CommentList/>
          </div>
          <div className="detail__module">
            <h3 className="detail__module--title"></h3>
            <div>
              <LineLink href="###" title=""/>
              <LineLink href="###" title=""/>
              <LineLink href="###" title=""/>
            </div>
          </div>
      </div>
        <Link to='/seat' className="detail__buyBtn"></Link>
        { showImage && <ImgSlide onClose={this.taggleImage}/>}
      </div>
    );
  }
}

export default Detail;



