import React,{Component} from 'react';
import './MovieItem.css';

class MovieItem extends  Component{
  render(){
    return (
      <div className="moveItem">
        <div className="moveItem__poster">
          <img src="./source/movie/333.jpg" alt=""/>
        </div>
        <div className="moveItem__details">
          <div className="moveItem__name"></div>
          <div className="moveItem__score"> <span>8.1</span></div>
          <div className="moveItem__director"></div>
          <div className="moveItem__actor"></div>
          <div className="moveItem__tag">
            <span className="tTag tTag--red"></span>
            <span className="tTag tTag--bule"></span>
          </div>
        </div>
        <div className="moveItem__btn">
          <button className="tBtn"></button>
          <span className="moveItem__price">9.9</span>
        </div>
      </div>
    )
  }
}
export default MovieItem;



